.footer-bottom {
  background: #ffffff;
  padding: 20px;
}

.footer-bottom p {
  padding: 0;
  margin: 0;
}

.footer-bottom .wrapper {
  display: flex;
  justify-content: space-between;
}

.footer-bottom .wrapper .socmed ul li {
  display: inline;
  margin-right: 10px;
}

.footer-bottom .wrapper .socmed ul li .socmed-item {
  font-size: 25px;
  color: #ffffff;
  background: #00cccc;
  padding: 5px;
  border-radius: 20%;
}
